@import "./colors.scss";
@import "./animations";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto";
}

.container {
  background-color: #a9df9b;
  width: 100%;
  height: 100vh;
}
.light {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: -6px;
    left: 50%;
    transform: translateX(-50%);
    width: 10px;
    height: 10px;
    border-radius: 3px;
    background-color: #545252;
  }
  //&:after {
  //  content: "";
  //  position: absolute;
  //  top: 50%;
  //  transform: translateY(-50%);
  //  left: 10%;
  //  display: block;
  //  width: 14px;
  //  height: 20px;
  //  border-radius: 50%;
  //  border-left: 2px solid #fff;
  //}
}
.lights__container {
  padding: 20px;
  display: flex;
  justify-content: space-around;
  background-image: url("../assets/branch.png");
  background-repeat-y: no-repeat;
}
.buttons-box {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}

.button {
  position: relative;
  z-index: 1;
  min-width: 50px;
  min-height: 50px;
  padding: 10px;
  font-size: 22px;
  font-weight: bold;
  border: 2px solid black;
  border-radius: 5px;
  background-color: white;
  cursor: pointer;
  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 2px;
    left: 2px;
    width: 100%;
    height: 100%;
    background-color: #e6bfef;
    border: 2px solid black;
    border-radius: 5px;
  }
}

.control {
  &__box {
    max-width: 800px;
    margin: 120px auto 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    font-size: 26px;
    font-weight: 400;
    color: white;
  }
}
